<template>
    <view class="grabOrder_hall">
        <cu-custom><block slot="content">抢单</block></cu-custom>
        <view class="hall_box">
            <!-- 导航栏 -->
            <scroll-view scroll-x class="bg-white nav">
                <view class="flex text-center navBox">
                    <view class="cu-item flex-sub" :class="item.id==tabCur?'things_active_color':''" v-for="(item,index) in tabData" :key="index" @tap="tabSelect" :data-id="item.id">
                        {{item.name}}
                    </view>
                </view>
            </scroll-view>
            <!-- 货物-列表 -->
            <scroll-view v-if="tabCur == 1"  class="freight_list" scroll-y="true">
                <view class="freight_item" v-for="(item,index) in freightData.list" :key="index" :data-id="item.orderId" @tap="linkToFreightInfo">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">出发城市</view>
                            <view class="start_value">{{item.loadingAddress}}</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../static/images/things/grabOrderHall/hall/arrow.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">到达城市</view>
                            <view class="arrive_value">{{item.unloadingAddress}}</view>
                        </view>
                    </view>
                    <!-- 货物信息 -->
                    <view class="freight_info">
                        <view class="things_width_full flex justify-start align-center user_info">
                            <view class="imageBox">
                                <image class="user_cover" :src="item.path" mode="widthFix"/>
                            </view>
                            <view class="name">{{item.nickname}}</view>
                            <view class="flex justify-start align-center score">
                                <image class="score_icon" src="./../../../../static/images/global/score_icon.png" mode="widthFix" />
                                <view class="score_num">{{item.reputation}}</view>
                                <view class="identity_text">{{item.userType}}</view>
                            </view>
                        </view>
                        <view class="car_single_info">货物名称：{{item.goodsName}}</view>
                        <view class="car_single_info">货物类型：{{item.goodsType}}</view>
                        <view class="car_single_info">运输价格：￥{{item.totalMoney}}</view>
                        <!-- <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物类型：{{item.goodsType}}</view>
                            <view class="freight_right_info">运输价格：￥{{item.totalMoney}}</view>
                        </view> -->
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <!-- <view class="freight_left_info">货物数量：234件</view> -->
                            <view class="freight_right_info">重量：{{item.weight}}</view>
                        </view>
                        <view class="freight_single_info">时间：{{item.startTime}}  - {{item.endTime}}</view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-between align-center operating">
                        <view class="time">{{item.createTime}}发布</view>
                        <button class="cu-btn bg-white grabOrderBtn" :disabled="buttonType" :data-id="item.orderId"  @tap.stop="linkToFreightFill">开始抢单</button>
                    </view>
                </view>
            </scroll-view>
            <!-- 货车-列表 -->
            <scroll-view v-else class="car_list" scroll-y="true">
                <view class="car_item" v-for="(item,index) in carData.list" :key="index" :data-id="item.orderId" @tap="linkToCarInfo">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">{{item.loadingAddress}}</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../static/images/things/grabOrderHall/hall/arrow.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">{{item.loadingAddress}}</view>
                        </view>
                    </view>
                    <!-- 货物信息 -->
                    <view class="car_info">
                        <view class="things_width_full flex justify-start align-center user_info">
                            <view class="imageBox">
                                <image class="user_cover" :src="item.path" mode="widthFix"/>
                            </view>
                            <view class="name">{{item.nickname}}</view>
                            <view class="flex justify-start align-center score">
                                <image class="score_icon" src="./../../../../static/images/global/score_icon.png" mode="widthFix" />
                                <view class="score_num">{{item.reputation}}</view>
                                <view class="identity_text">{{item.userType}}</view>
                            </view>
                        </view>
                        <view class="things_width_full flex justify-start align-center car_double_info">
                            <view class="car_left_info">货物类型：{{item.carsType}}</view>
                            <view class="car_right_info">运输价格：￥{{item.totalMoney}}</view>
                        </view>
                        <view class="things_width_full flex justify-start align-center car_double_info">
                            <view class="car_left_info">罐体立方数：{{item.cubicNumber}}</view>
                            <view class="car_right_info">车内灌质：{{item.carFilling}}</view>
                        </view>
                        <view class="car_single_info">服务日期：{{item.startTime}} - {{item.endTime}}</view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-between align-center operating">
                        <view class="time">{{item.createTime}}发布</view>
                        <button class="cu-btn bg-white grabOrderBtn" :disabled="buttonType" :data-id="item.orderId" @tap.stop="linkToCarFill">开始抢单</button>
                    </view>
                </view>
            </scroll-view>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
import { releaseCarList_api,releaseGoodsList_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
	let that = null;
export default {
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            tabCur: 1,
            scrollLeft: 0,
            tabData: [
                {
                    id: 1,
                    name: '货源列表'
                },
                {
                    id: 2,
                    name: '车源列表'
                }
            ],
            freightData: {//货物单
                page: 1,
                page_size: 10,
                list: [],
                pageMore: true,
            },
            carData: {//货车单
                page: 1,
                page_size: 10,
                list: [],
                pageMore: true,
            },
            buttonType: false,
        }
    },
    watch: {
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        clearPageFun(e) {
            that.carData = {
                page: 1,
                page_size: 10,
                list: [],
                pageMore: true,
            };
            that.freightData = {
                page: 1,
                page_size: 10,
                list: [],
                pageMore: true,
            };
        },
        /**导航选择
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        tabSelect(e) {
            this.tabCur = e.currentTarget.dataset.id;
            this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
        },
        /**跳转到抢车单钱填表
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToCarFill(e) {
            that.base({name:'buttonType',value:true});
            uni.navigateTo({
                url: `/pages/things/grabOrderHall/car/fill/index?order_id=${e.currentTarget.dataset.id}`,
                complete: ()=>{
                    that.base({name:'buttonType',value:false});
                }
            });
        },
        /**跳转到抢货单前填表
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToFreightFill(e) {
            that.base({name:'buttonType',value:true});
            uni.navigateTo({
                url: `/pages/things/grabOrderHall/freight/fill/index?order_id=${e.currentTarget.dataset.id}`,
                complete: ()=>{
                    that.base({name:'buttonType',value:false});
                }
            });
        },
        /**跳转到货物详情
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToFreightInfo(e) {
            that.base({name:'buttonType',value:true});
            uni.navigateTo({
                url: `/pages/things/grabOrderHall/freight/info/index?order_id=${e.currentTarget.dataset.id}`,
                fail: (err)=>{
                    console.log(err);
                },
                complete: ()=>{
                    that.base({name:'buttonType',value:false});
                }
            });
        },
        /**跳转到货车详情
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToCarInfo(e) {
            that.base({name:'buttonType',value:true});
            uni.navigateTo({
                url: `/pages/things/grabOrderHall/car/info/index?order_id=${e.currentTarget.dataset.id}`,
                complete: ()=>{
                    that.base({name:'buttonType',value:false});
                }
            });
        },
        /**获取抢单货物列表
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getFreightList(e) {
            uni.showLoading({
                title: '加载中'
            });
            let params = {
                page: that.freightData.page,
                page_size: that.freightData.page_size
            };
            releaseGoodsList_api(params,that).then(res=>{
                that.freightData.list = that.freightData.list.concat(res.list);
                if (res.total >= that.freightData.list.length) {
                    that.freightData.pageMore = false;
                }else{
                    that.freightData.page++;
                }
                uni.hideLoading();
            }).catch(err=>{
                console.log(err);
                uni.hideLoading();
            })
        },
        /**获取抢单货车列表
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getCarList(e) {
            uni.showLoading({
                title: '加载中'
            });
            let params = {
                page: that.carData.page,
                page_size: that.carData.page_size
            };
            releaseCarList_api(params,that).then(res=>{
                that.carData.list = that.carData.list.concat(res.list);;
                if (res.total <= that.carData.list.length) {
                    that.carData.pageMore = false;
                }else{
                    that.carData.page++;
                }
                uni.hideLoading();
            }).catch(err=>{
                console.log(err);
                uni.hideLoading();
            })
        },
        /**货物上拉加载
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        onReachBottomFreight(e) {
            if (that.freightData.pageMore) that.getFreightList();
        },
        /**货车上拉加载
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        onReachBottomCar(e) {
            if (that.carData.pageMore) that.getCarList();
        },
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        that.tabCur = options.type;
        that.getFreightList();
        that.getCarList();
    }
}
</script>

<style lang="scss" scoped>
    @import "./index";
</style>